* {
  font-family: "Rubik", sans-serif;
}

body {
  text-decoration: none;
  background-color: #e0e5ec;
  margin: 0;
  padding: 0;
  background-image: url(data:image/png;base64,);
  scrollbar-width: none;
}

body::-webkit-scrollbar {
  display: none;
  /* for Chrome, Safari, and Opera */
}

.bar {
  width: 100%;
  height: 7vh;
  background-color: #111b21d3;
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  z-index: 5;
}

.profile {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
}

.blur {
  border-radius: 50%;
  -webkit-filter: blur(212px);
  filter: blur(212px);
  -webkit-filter: blur(150px);
  filter: blur(150px);
  z-index: -9;
  display: flex;
  animation-name: my-animation;
  animation-duration: 2s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes my-animation {
  0% {
    background: rgba(252, 98, 9, 0.69);
  }

  60% {
    background: rgba(252, 98, 9, 0.69);
  }

  100% {
    background: rgba(252, 98, 9, 0);
  }
}

.plans-blur-1 {
  height: 23rem;
  width: 32rem;
  left: 20%;
  justify-content: center;
}

.profilimg {
  position: absolute;
  background-color: #e0e5ec;
  padding: 10px;
  height: 30vh;
  border-radius: 100%;
  text-align: center;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.profileimg {
  width: 30vh;
  border-bottom-left-radius: 16vh;
  border-bottom-right-radius: 16vh;
  margin-top: -17vh;
}

.name {
  margin-top: 5vh;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
  color: rgba(240, 248, 255, 0);
  -webkit-text-stroke: 2px aliceblue;
  font-size: 3rem;
}

.Compain {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
  margin: 0%;
  padding: 0%;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
  color: rgba(240, 248, 255, 0);
  -webkit-text-stroke: 2px aliceblue;
  font-size: 3rem;
  margin-left: 1vw;
}

.Compain-Name {
  background-color: rgba(0, 255, 255, 0);
}

.menu {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  color: aliceblue;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.menu span {
  transition: 2s;
  padding-top: 1.5vh;
  padding-bottom: 1.5vh;
  padding-left: 2vw;
  padding-right: 2vw;
  background-color: #5d50503e;
  border: 1.5px solid rgba(240, 248, 255, 0);
  margin-left: 2vw;
  border-radius: 10px;
  color: aliceblue;
}

.menu span:hover {
  transition: 2s;
  background-color: #5d505000;
  color: aliceblue;
  border: 1.5px solid rgb(240, 248, 255);
}

a {
  text-decoration: none;
  color: #e0e5ec;
}

.about {
  background-color: #e0e5ec;
  text-align: center;
}

.section {
